import React, { Component } from 'react'
import PropTypes from 'prop-types'
import searchImg from '../../static/search.png'
import '../../css/MenuBar.css'

class MenuBar extends Component {
  constructor() {
    super()

    this.handleWindowResize = this.handleWindowResize.bind(this)
  }

  componentWillMount() {
    this.handleWindowResize()
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleWindowResize)
    window.addEventListener('orientationchange', this.handleWindowResize)
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowResize)
    window.removeEventListener('orientationchange', this.handleWindowResize)
  }

  handleWindowResize() {
    const w = window
    const doc = document
    const docElem = doc.documentElement
    const menubarElems = doc.getElementsByClassName('menubar')

    if (menubarElems.length > 0) {
      const menubarStyles = w.getComputedStyle(menubarElems[0])

      if (menubarStyles) {
        docElem.style.setProperty('--menubar-height', menubarStyles.height)
      }
    }
  }

  render() {
    return (
      <div className="menubar">
        <div className="menubar-left">
          <h1>[[PC_APP]]</h1>
        </div>
        <div className="menubar-right">
          <button className="newgame" onClick={this.props.handleQueryToggle}><img src={searchImg} alt='search'/> <span>Search</span></button>
        </div>
      </div>
    )
  }
}

MenuBar.propTypes = {
  handleQueryToggle: PropTypes.func
}

export default MenuBar


